body {
  margin: 0;
  font-size: 16px;
  main {
    .home-container {
      .home-bg {
        height: 100vh;
      }
      .banner-content {
        color: #fff;
        position: absolute;
        text-align: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        > h1 {
          font-size: 62px;
          font-weight: 500;
          color: #fff;
          margin-bottom: 40px;
          span {
            &:first-child {
              margin-right: 20px;
            }
          }
        }
      }
    }
  }
}
